import React from "react";
import { Field, reduxForm } from "redux-form";
import { requiredField } from "../../utils/validators/validators";
import { Input } from "../common/FormsControls/FormsControl";
import classLoginForm from "../common/FormsControls/FormsControl.module.css";

const LogInForm = ({ handleSubmit, error, captchaURL }) => {
    //  <form>   onSubmit (    )
    return <form onSubmit={handleSubmit}>
        <div>
            {/* Field -   ,    props, 
             component={'input'} -      HTML.
            name -     .
             devConsole  store.getState().form  ,   state     . */}
            <Field placeholder="Login" name='email'
                component={Input} validate={[requiredField]} />
        </div>
        <div>
            <Field placeholder="Password" name='password' type='password'
                component={Input} validate={[requiredField]} />
        </div>
        <div>
            <Field type="checkbox" name='rememberMe' component={Input} />Remember me
        </div>
        {error && <div className={classLoginForm.formSummaryError}>
            {error}
        </div>}
        {captchaURL &&
            <div>
                <img src={captchaURL} alt='' />
                <div>
                    <Field placeholder="Enter symbols from captcha" name='captcha'
                        component={Input} />
                </div>
            </div>}
        <div>
            <button>Login</button>
        </div>
    </form>
}

// reduxForm      . ..   . 
const LoginReduxForm = reduxForm({
    //a unique name for the form
    form: 'login'
})(LogInForm)

export default LoginReduxForm